@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap);
*{margin:0;padding:0;font-family:'Mulish', sans-serif;box-sizing:border-box}.navbar{background-color:#fff !important}.navbar a{font-size:1.3rem !important;text-transform:capitalize;color:#8000f8 !important}.navbar a:hover{color:#0415af !important}.navbar .navbar-brand{color:#8000f8 !important;font-size:1.9rem !important;font-weight:bold}.menu_active{font-weight:bold;border-bottom:1px solid brown !important}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:black}.brand-name{color:#8000f8}#header h2{font-size:48px;color:black}.btn-get-started{font-size:16px;font-weight:500;padding:10px 30px;border:2px solid #8000f8;border-radius:50px;color:#8000f8;text-decoration:none;letter-spacing:1px;transition:1s;display:inline-block}.btn-get-started:hover{background-color:#8000f8;color:#fff}.header-img{text-align:right}.animated{animation:up-down 2s ease-in-out infinite alternate-reverse both;width:350px;height:450px !important}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}.card-imgg{width:100%;height:200px}.footer-div{background-color:#8000f8;color:#fff}

